<template>
  <div 
    class="floating-buttons-cloud" 
    :style="{ backgroundImage: `url(${logo})` }"
    ref="cloudContainer"
    @touchstart="handleTouchStart"
    @touchmove="handleTouchMove"
    @touchend="handleTouchEnd"
  >
    <div 
      class="button-item do-invert" 
      v-for="(button, index) in buttons" 
      :key="`btn-${index}`"
      :style="getButtonStyle(index)"
      @mouseenter="handleHover(index, true)"
      @mouseleave="handleHover(index, false)"
    >
      <router-link :to="button.to" class="cloud-button">
        <i :class="button.icon"></i> {{ button.text }}
      </router-link>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'
import logo from '@/assets/home-hero-cat.png'

const cloudContainer = ref(null)
let animationPaused = false
let currentTime = ref(0)
let animationId = null
let hoveredIndex = ref(-1)

// Touch —É–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤
let touchStartX = 0
let touchStartY = 0
let touchStartTime = 0
let manualOffset = ref(0) // –°–º–µ—â–µ–Ω–∏–µ –æ—Ç touch –∂–µ—Å—Ç–æ–≤
let isMobile = ref(false)

const buttons = [
{ to: '/ai-agents', icon: 'fas fa-robot', text: 'AI Agents' },
  { to: '/nft', icon: 'fas fa-image', text: 'NFT Space' },
  { to: '/tokens', icon: 'fas fa-rocket', text: 'Token Launchpad' },
  { to: '/cashback', icon: 'fas fa-gift', text: 'SOL Cashback' },
  { to: '/dao', icon: 'fas fa-vote-yea', text: 'DAO Voting' },
  { to: '/games', icon: 'fas fa-gamepad', text: 'Games Arena' },
  { to: '/my/referrals', icon: 'fas fa-user', text: 'Referrals' },
  { to: '/rewards', icon: 'fas fa-trophy', text: 'Rewards' },
  { to: '/welcome', icon: 'fas fa-plus', text: 'More...' }
]

const handleHover = (index, isHovering) => {
  hoveredIndex.value = isHovering ? index : -1
  
  // –û—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞–Ω–∏–º–∞—Ü–∏—é —Ç–æ–ª—å–∫–æ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ –Ω–∞ –∫–Ω–æ–ø–∫—É
  if (isHovering) {
    animationPaused = true
    if (animationId) {
      cancelAnimationFrame(animationId)
    }
  } else {
    animationPaused = false
    animate()
  }
}

const animate = () => {
  if (animationPaused) return
  
  // üéõÔ∏è –°–ö–û–†–û–°–¢–¨ –ê–ù–ò–ú–ê–¶–ò–ò - —á–µ–º –±–æ–ª—å—à–µ –∑–Ω–∞—á–µ–Ω–∏–µ, —Ç–µ–º –±—ã—Å—Ç—Ä–µ–µ –¥–≤–∏–∂–µ–Ω–∏–µ
  // –î–∏–∞–ø–∞–∑–æ–Ω: 0.001 (–æ—á–µ–Ω—å –º–µ–¥–ª–µ–Ω–Ω–æ) - 0.02 (–±—ã—Å—Ç—Ä–æ)
  currentTime.value += 0.0035
  animationId = requestAnimationFrame(animate)
}

const getButtonStyle = (index) => {
  // –¶–µ–Ω—Ç—Ä –æ–±–ª–∞–∫–∞ (–¥–∏–Ω–∞–º–∏—á–µ—Å–∫–∏ –Ω–∞ –æ—Å–Ω–æ–≤–µ —Ä–∞–∑–º–µ—Ä–æ–≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞)
  const containerRect = cloudContainer.value?.getBoundingClientRect()
  const centerX = containerRect ? containerRect.width / 2 : 280 // –ü–æ–ª–æ–≤–∏–Ω–∞ —à–∏—Ä–∏–Ω—ã –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
  const centerY = containerRect ? containerRect.height / 2 : 100 // –ü–æ–ª–æ–≤–∏–Ω–∞ –≤—ã—Å–æ—Ç—ã –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
  
  // üéõÔ∏è –†–ê–ó–ú–ï–†–´ –≠–õ–õ–ò–ü–°–ê - –Ω–∞—Å—Ç—Ä–æ–π–∫–∞ —Ñ–æ—Ä–º—ã –æ—Ä–±–∏—Ç—ã
  const radiusX = containerRect ? Math.min(containerRect.width, containerRect.height) * 0.6 : 150 // –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Ä–∞–¥–∏—É—Å (—à–∏—Ä–∏–Ω–∞ –æ—Ä–±–∏—Ç—ã) 0.1-0.5
  const radiusY = containerRect ? Math.min(containerRect.width, containerRect.height) * 0.3 : 60 // –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω—ã–π —Ä–∞–¥–∏—É—Å (–≤—ã—Å–æ—Ç–∞ –æ—Ä–±–∏—Ç—ã) 0.05-0.3
  
  const angle = (index / buttons.length) * Math.PI * 2 + currentTime.value + manualOffset.value
  const x = centerX + Math.cos(angle) * radiusX
  const y = centerY + Math.sin(angle) * radiusY
  
  // üéõÔ∏è –ü–ï–†–°–ü–ï–ö–¢–ò–í–ê - –Ω–∞—Å—Ç–æ—è—â–∏–π —ç—Ñ—Ñ–µ–∫—Ç –≥–ª—É–±–∏–Ω—ã (—Ç–æ–ª—å–∫–æ —Ä–∞–∑–º–µ—Ä)
  // Math.sin(angle): -1 (–≤–µ—Ä—Ö —ç–ª–ª–∏–ø—Å–∞, —Å–∑–∞–¥–∏) –¥–æ +1 (–Ω–∏–∑ —ç–ª–ª–∏–ø—Å–∞, —Å–ø–µ—Ä–µ–¥–∏)
  const perspectiveFactor = Math.sin(angle) // –û—Ç -1 (—Å–∑–∞–¥–∏) –¥–æ +1 (—Å–ø–µ—Ä–µ–¥–∏)
  const minScale = 0.4 // üéõÔ∏è –†–∞–∑–º–µ—Ä —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å–∑–∞–¥–∏: 0.1-0.6
  const maxScale = 1.0 // üéõÔ∏è –†–∞–∑–º–µ—Ä —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å–ø–µ—Ä–µ–¥–∏: 0.8-1.5
  const scale3D = minScale + (perspectiveFactor + 1) * (maxScale - minScale) / 2
  
  // üéõÔ∏è HOVER –≠–§–§–ï–ö–¢ - –≤—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã –ø—Ä–∏ hover —Å—Ç–∞–Ω–æ–≤—è—Ç—Å—è —Ä–∞–∑–º–µ—Ä–æ–º 1.1
  const hoverScale = 1.1 // üéõÔ∏è –ê–±—Å–æ–ª—é—Ç–Ω—ã–π —Ä–∞–∑–º–µ—Ä –ø—Ä–∏ hover 0.8-1.5
  const finalScale = hoveredIndex.value === index ? hoverScale : scale3D
  
  return {
    left: `${x}px`,
    top: `${y}px`,
    transform: `translate(-50%, -50%) scale(${finalScale})`
  }
}

// –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –º–æ–±–∏–ª—å–Ω–æ–≥–æ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
const checkMobile = () => {
  isMobile.value = window.innerWidth <= 768
}

// Touch —Å–æ–±—ã—Ç–∏—è –¥–ª—è –º–æ–±–∏–ª—å–Ω–æ–≥–æ —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è
const handleTouchStart = (e) => {
  touchStartX = e.touches[0].clientX
  touchStartY = e.touches[0].clientY
  touchStartTime = Date.now()
}

const handleTouchMove = (e) => {
  e.preventDefault() // –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ–º —Å–∫—Ä–æ–ª–ª —Å—Ç—Ä–∞–Ω–∏—Ü—ã
}

// üéõÔ∏è TOUCH –ù–ê–°–¢–†–û–ô–ö–ò –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö –∂–µ—Å—Ç–æ–≤
const handleTouchEnd = (e) => {
  const touchEndX = e.changedTouches[0].clientX
  const touchEndY = e.changedTouches[0].clientY
  const touchDuration = Date.now() - touchStartTime
  
  const deltaX = touchEndX - touchStartX
  const deltaY = touchEndY - touchStartY
  const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY)
  
  // üéõÔ∏è –ü–ê–†–ê–ú–ï–¢–†–´ –°–í–ê–ô–ü–ê
  const minSwipeDistance = 50 // üéõÔ∏è –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è –¥–∏—Å—Ç–∞–Ω—Ü–∏—è —Å–≤–∞–π–ø–∞ (px) 30-100
  const maxSwipeDuration = 300 // üéõÔ∏è –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–µ –≤—Ä–µ–º—è —Å–≤–∞–π–ø–∞ (ms) 200-500
  const swipeStrength = 0.5 // üéõÔ∏è –°–∏–ª–∞ –≤–ª–∏—è–Ω–∏—è —Å–≤–∞–π–ø–∞ –Ω–∞ –¥–≤–∏–∂–µ–Ω–∏–µ 0.1-1.0
  
  // –ï—Å–ª–∏ —ç—Ç–æ —Å–≤–∞–π–ø (–±—ã—Å—Ç—Ä–æ–µ –¥–≤–∏–∂–µ–Ω–∏–µ –Ω–∞ –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ–µ —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ)
  if (distance > minSwipeDistance && touchDuration < maxSwipeDuration) {
    const swipeDirection = Math.atan2(deltaY, deltaX)
    // –î–æ–±–∞–≤–ª—è–µ–º —Å–º–µ—â–µ–Ω–∏–µ –≤ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–∏ —Å–≤–∞–π–ø–∞
    manualOffset.value += swipeDirection * swipeStrength
  }
}

onMounted(() => {
  checkMobile()
  window.addEventListener('resize', checkMobile)
  animate()
})

onUnmounted(() => {
  if (animationId) {
    cancelAnimationFrame(animationId)
  }
  window.removeEventListener('resize', checkMobile)
})
</script>

<style scoped>
.floating-buttons-cloud {
  position: relative;
  width: 100%;
  max-width: 560px;
  height: 300px;
  margin: 0 auto;
  /* overflow: hidden; */
  background-color: transparent;
  /* background-image: url('https://solanabitcat.com/data/logo.png'); */
  background-size: 300px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.button-item {
  position: absolute;
  transition: transform 0.3s ease;
  will-change: transform;
}

.button-item:hover {
  z-index: 100 !important;
}

.cloud-button {
  padding: 12px 20px;
  border-radius: 30px;
  font-weight: 600;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: 1.1rem;
  background: linear-gradient(135deg, #ff9800 0%, #ff5722 100%);
  color: white;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.3s ease;
}

.button-item:hover .cloud-button {
  box-shadow: 0 8px 30px rgba(255, 152, 0, 0.6);
}

@media (max-width: 768px) {
  .floating-buttons-cloud {
    width: 75%;
    touch-action: none; /* –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–µ touch –∂–µ—Å—Ç—ã */
    background-size: 250px;
  }
  
  .cloud-button {
    padding: 6px 12px;
    gap: 4px;
  }
}
</style> 